import React, {Component} from "react"
import PropTypes from "prop-types";

export class DatePicker extends Component {
    constructor(props) {
        super(props);
        this.onChangeDate = this.onChangeDate.bind(this);
        this.onChangeTime = this.onChangeTime.bind(this);
        this.onChangeValue = this.onChangeValue.bind(this);
    }

    getDatepickerOptions(props) {
        // overriding hasIcon because it's modifying DOM, we'd rather do that ourselves
        return Object.assign({hasIcon: false}, props.options);
    }

    componentDidMount() {
        OPL.UI.on(OPL.UI.EVENTS.modules.datepicker.selectedDate, this.onChangeDate, this.props.id);
        OPL.UI.on(OPL.UI.EVENTS.modules.datepicker.selectedTime, this.onChangeTime, this.props.id);
        let modulesToLoad = [{
            path: 'common/modules/opl-datepicker',
            options: this.getDatepickerOptions(this.props)
        }];
        if (this.props.mask && this.props.placeholder !== undefined) {
            modulesToLoad.push({
                path:'common/modules/opl-input-mask',
                options: {
                    "mask": this.props.mask,
                    "placeholder": this.props.placeholder,
                    "showMaskOnHover": this.props.showMaskOnHover
                }
            });
        }
        OPL.UI.loadModules(this.refs.datepicker, modulesToLoad);
    }

    componentWillUnmount() {
        OPL.UI.off(OPL.UI.EVENTS.modules.datepicker.selectedDate, this.onChangeDate, this.props.id);
        OPL.UI.off(OPL.UI.EVENTS.modules.datepicker.selectedTime, this.onChangeTime, this.props.id);
    }

    componentWillReceiveProps(newProps) {
        for (var opt in newProps.options) {
            if (this.props.options[opt] !== newProps.options[opt]) {
                OPL.UI.fire(OPL.UI.EVENTS.modules.datepicker.loaddata, this.getDatepickerOptions(newProps), this.props.id);
                break;
            }
        }
    }

    onChangeDate(date) {
        if (this.props.onChange) {
            this.props.onChange({id: this.props.id, name: this.props.name, value: this.refs.datepicker.value, date: date});
        }
    }

    onChangeTime(date) {
        if (this.props.onChange) {
            this.props.onChange({id: this.props.id, name: this.props.name, value: this.refs.datepicker.value, date: date});
        }
    }

    onChangeValue(event) {
        let {id, name, value} = event.target;
        if (this.props.onChange) {
            this.props.onChange({id, name, value});
        }
    }

    onBlurValue(event) {
        let {id, name, value} = event.target;
        if (this.props.onBlur) {
            this.props.onBlur({id, name, value});
        }
    }

    getError() {
        if(this.props.errors && this.props.errors.length > 0){
            return this.props.errors[0];
        }
        return null;
    }

    renderContent() {
        return ([
            <input type="text" id={this.props.id} value={this.props.value} ref="datepicker" onChange={this.onChangeValue} onBlur={this.onBlurValue.bind(this)} className={this.props.inputClassName} disabled={this.props.disabled}/>,
            !this.props.icon ||
            <label htmlFor={this.props.id} className={"calendar-icon g-icon g-icon--" + this.props.icon}>
                <span className="u-acc-hide">Poka≈º kalendarz</span>
            </label>

        ]);
    }

    render() {
        let error = this.getError.apply(this);
        return (
            <div className="opl-datepicker">
                { this.props.floatingLabel ?
                    <FloatingLabelForDatePicker {...this.props}>
                        { this.renderContent() }
                    </FloatingLabelForDatePicker>
                :
                    this.renderContent()
                }
                {error && <div id={this.props.id} className={'opl-msg opl-msg--context u-margin-top-s opl-msg--' + error.level}>
                    <p>{error.message}</p>
                </div>}
            </div>
        );
    }
}

DatePicker.propTypes = {
    id: PropTypes.string.isRequired,
    value: PropTypes.string,
    onChange: PropTypes.func,
    onBlur: PropTypes.func,
    icon: PropTypes.string,
    floatingLabel: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
    inputClassName: PropTypes.string,
    // opl-datepicker options
    options: PropTypes.shape({
        disabledDates: PropTypes.array,
        datepicker: PropTypes.bool,
        timepicker: PropTypes.bool,
        minDate: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
        maxDate: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
        format: PropTypes.string,
        formatDate: PropTypes.string,
        step: PropTypes.number,
        startDate: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
        defaultDate: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
        defaultTime: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
        disabledWeekDays: PropTypes.arrayOf(PropTypes.number),
        allowDates: PropTypes.arrayOf(PropTypes.string),
        allowTimes: PropTypes.arrayOf(PropTypes.string),
        hideOtherMonth: PropTypes.bool,
        scrollInput: PropTypes.bool,
        yearStart: PropTypes.number,
        yearEnd: PropTypes.number
    })
};

DatePicker.defaultProps = {
    icon: "calendar3",
    floatingLabel: false,
    options: {},
    showMaskOnHover: false
};

export class FloatingLabelForDatePicker extends Component {
    componentDidMount() {
        OPL.UI.loadModules(this.refs.labelModule, [{
            path: 'core/modules/floating-label',
            options: {}
        }]);
    }

    render() {
        return (
            <div ref="labelModule" className="o-floating-label">
                { this.props.children }
                <p className="label o-floating-label__placeholder">{this.props.floatingLabel}</p>
            </div>
        )
    }
}
